  margin: 0;
  padding: 0;
  min-height: 100vh;
  background: linear-gradient(
    to bottom,
    #0f172a,
    #1e293b
  ); /* dark blue gradient */
  font-family: "Orbitron", sans-serif; /* clean, futuristic font */
  color: #f1f5f9; /* light text for contrast */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
  padding-top: 30px;
}
.title {
  text-align: center;
}
.game-body {
  margin: 2%;
  display: grid;
  grid-template-rows: 0.2fr 0.8fr;
}
.board-titles {
  display: flex;
  justify-content: space-between;
  margin-left: 5%;
  margin-right: 5%;
}
.board-container {
  display: flex;
  justify-content: space-around;
}
.user-board,
.openant-board {
  margin: 1%;
  height: fit-content;
  width: fit-content;
  display: grid;
  grid-template-columns: repeat(10, 1fr);
}
/* ---------- multi Purpose Board ------ */
.multi-purpose-board {
  min-width: 250px;
  max-width: 400px;

  margin: 1%;
}

.cell {
  width: 50px;
  height: 50px;
  background-color: #1e293b; /* dark navy blue */
  border: 1px solid #334155; /* slightly lighter border for contrast */
  box-sizing: border-box;
  transition: background-color 0.2s ease, transform 0.1s ease;
  cursor: pointer;
  border-radius: 6px; /* added rounded corners */
}
.cell:hover {
  background-color: #334155; /* slightly brighter on hover */
}


.Carrier,
.Cruiser,
.BattleShip,
.Submarine,
.Destroyer {
  border-radius: 10px;
  background-color: rgb(0, 0, 0);
}

.ship-container {
  display: flex;
  flex-direction: row;
  height: fit-content;
}
.user_ship_button_container {
  display: grid;
  grid-template-columns: 0.2fr 0.8fr;
  grid-template-rows: 0.6fr 0.2fr repeat(5, 0.4fr);
  gap: 5px 10px;
}
.spacebarInstruction{
  grid-column: span 2;
  background-color: #0f172a;
  color: #facc15; /* yellow text */
  border: 2px dashed #facc15;
  padding: 8px;
  font-size: 1.1rem;
  text-align: center;
  border-radius: 8px;
  box-shadow: 0 0 8px #facc15;
}

.ship-button,
.readyButton {
  background-color: #1f2937; /* Dark navy background */
  color: #f9fafb; /* Light text */
  border: 2px solid #4ade80; /* Green glow border */
  padding: 12px;
  height: 50px;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  box-shadow: 0 0 0px #4ade80;
}
.ship-button:active {
  transform: scale(0.97);
  background-color: #0f172a;
  box-shadow: 0 0 4px #22c55e;
}
.ship-button:hover {
  background-color: #111827;
  box-shadow: 0 0 10px #4ade80, 0 0 20px #4ade80;
  transform: scale(1.05);
}
.headingTextForShipButtons,
.readyHeading,
.shipsLeftHeading,
.winner-message {
  grid-column: span 2;
  background-color: #0f172a; /* Deep navy */
  color: #38bdf8; /* Bright blue for command tone */
  border: 2px solid #38bdf8;
  padding: 5px;
  text-align: center;
  font-size: 1.5rem;

  border-radius: 10px;
  box-shadow: 0 0 10px #38bdf8;
  margin-bottom: 10px;
  max-width: 500px;
  text-shadow: 0 0 5px #38bdf8;
  letter-spacing: 1px;
}
.ship_placed_button {
  background-color: #7fff00;
  color: #1e293b;
  box-shadow: 0 0 5px #7fff00;
}
.readyButton {
  display: flex;
  justify-self: center;
  margin-top: 10%;
}
.readyButton:hover {
  background-color: greenyellow;
}
/* ----------------------------- Game Status Div -------------------------------------- */
.gameStatusContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.shipsLeftHeading {
  grid-column: span 2;
}
/* attacks display */
.ship-attacked {
  background-color: red;
}
.missed-attack {
  background-color: #226bc5;
}
.no-clicks {
  pointer-events: none;
}
.disableBoard {
  position: relative;
  pointer-events: none;
  z-index: 10;
}
.disableBoard::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); /* semi-transparent overlay */
  z-index: 2;
}
.hoverUpdate{
  background-color: rgba(172, 255, 47, 0.3);
  border: 2px solid green;
  box-shadow: 0 0 5px rgba(172, 255, 47, 0.6);
}

.notValidPlacement{
  background-color: rgba(255, 0, 0, 0.3);
  border: 2px solid red;
  box-shadow: 0 0 5px rgba(255, 0, 0, 0.6);
}
.enemy-title{
  display: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;EACE,SAAS;EACT,UAAU;EACV,iBAAiB;EACjB;;;;GAIC,EAAE,uBAAuB;EAC1B,mCAAmC,EAAE,2BAA2B;EAChE,cAAc,EAAE,4BAA4B;EAC5C,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,2BAA2B;EAC3B,SAAS;EACT,iBAAiB;AACnB;AACA;EACE,kBAAkB;AACpB;AACA;EACE,UAAU;EACV,aAAa;EACb,+BAA+B;AACjC;AACA;EACE,aAAa;EACb,8BAA8B;EAC9B,eAAe;EACf,gBAAgB;AAClB;AACA;EACE,aAAa;EACb,6BAA6B;AAC/B;AACA;;EAEE,UAAU;EACV,mBAAmB;EACnB,kBAAkB;EAClB,aAAa;EACb,sCAAsC;AACxC;AACA,0CAA0C;AAC1C;EACE,gBAAgB;EAChB,gBAAgB;;EAEhB,UAAU;AACZ;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,yBAAyB,EAAE,mBAAmB;EAC9C,yBAAyB,EAAE,yCAAyC;EACpE,sBAAsB;EACtB,2DAA2D;EAC3D,eAAe;EACf,kBAAkB,EAAE,0BAA0B;AAChD;AACA;EACE,yBAAyB,EAAE,+BAA+B;AAC5D;;;AAGA;;;;;EAKE,mBAAmB;EACnB,8BAA8B;AAChC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,mBAAmB;AACrB;AACA;EACE,aAAa;EACb,kCAAkC;EAClC,gDAAgD;EAChD,aAAa;AACf;AACA;EACE,mBAAmB;EACnB,yBAAyB;EACzB,cAAc,EAAE,gBAAgB;EAChC,0BAA0B;EAC1B,YAAY;EACZ,iBAAiB;EACjB,kBAAkB;EAClB,kBAAkB;EAClB,2BAA2B;AAC7B;;AAEA;;EAEE,yBAAyB,EAAE,yBAAyB;EACpD,cAAc,EAAE,eAAe;EAC/B,yBAAyB,EAAE,sBAAsB;EACjD,aAAa;EACb,YAAY;EACZ,eAAe;EACf,iBAAiB;EACjB,kBAAkB;EAClB,eAAe;EACf,gCAAgC;EAChC,2BAA2B;AAC7B;AACA;EACE,sBAAsB;EACtB,yBAAyB;EACzB,2BAA2B;AAC7B;AACA;EACE,yBAAyB;EACzB,8CAA8C;EAC9C,sBAAsB;AACxB;AACA;;;;EAIE,mBAAmB;EACnB,yBAAyB,EAAE,cAAc;EACzC,cAAc,EAAE,iCAAiC;EACjD,yBAAyB;EACzB,YAAY;EACZ,kBAAkB;EAClB,iBAAiB;;EAEjB,mBAAmB;EACnB,4BAA4B;EAC5B,mBAAmB;EACnB,gBAAgB;EAChB,4BAA4B;EAC5B,mBAAmB;AACrB;AACA;EACE,yBAAyB;EACzB,cAAc;EACd,2BAA2B;AAC7B;AACA;EACE,aAAa;EACb,oBAAoB;EACpB,eAAe;AACjB;AACA;EACE,6BAA6B;AAC/B;AACA,yFAAyF;AACzF;EACE,aAAa;EACb,8BAA8B;AAChC;AACA;EACE,mBAAmB;AACrB;AACA,oBAAoB;AACpB;EACE,qBAAqB;AACvB;AACA;EACE,yBAAyB;AAC3B;AACA;EACE,oBAAoB;AACtB;AACA;EACE,kBAAkB;EAClB,oBAAoB;EACpB,WAAW;AACb;AACA;EACE,WAAW;EACX,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,QAAQ;EACR,SAAS;EACT,8BAA8B,EAAE,6BAA6B;EAC7D,UAAU;AACZ;AACA;EACE,yCAAyC;EACzC,uBAAuB;EACvB,2CAA2C;AAC7C;;AAEA;EACE,sCAAsC;EACtC,qBAAqB;EACrB,wCA